/*dropdown button*/
.trigger {
  box-sizing: content-box;
  width: auto;
  height: auto;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-bottom: 10px solid transparent;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  max-height: 40%; }
  .trigger:hover {
    cursor: pointer; }

.dropdown-toggle {
  display: block;
  width: 12px;
  height: 12px;
  background: transparent;
  border-left: none;
  border-bottom: 2px solid white;
  border-top: none;
  border-right: 2px solid white;
  transform: rotate(-45deg);
  box-shadow: none;
  padding: 0;
  line-height: 0;
  border-radius: 0;
  margin-top: 15px;
  transform-origin: center; }
  .dropdown-toggle:focus {
    outline: none; }
  .dropdown-toggle:hover {
    box-shadow: none; }

.dropdown-open {
  transform: rotate(45deg); }

